<template>
  <v-card-text>
    <vertical-height-container vh="89">
      <template v-slot:children>
        <page-title title="Network Interface"></page-title>
        <v-spacer></v-spacer>
        <v-data-table :headers="headers" :items="data" class="elevation-1" item-key="uuid" show-select disable-sort>
          <template v-slot:no-data> No data </template>
          <template v-slot:[`item.tools`]="{ item }">
            <v-btn icon small @click="() => onClickEdit(item)">
              <v-icon small dark> mdi-square-edit-outline </v-icon>
            </v-btn>
          </template>
          <template v-slot:[`item.interface`]="{ item }">
            {{ displayInterface(item.interface) }}
          </template>
        </v-data-table>
        <page-title title="Security Rules"></page-title>

        <v-row style="margin-bottom: 20px">
          <v-col>
            <v-card elevation="2" outlined tile class="card-height">
              <v-card-title>
                <h5>Firewall</h5>
                <v-row align="center" justify="end">
                  <v-avatar size="35">
                    <img alt="logo" :src="logo" />
                  </v-avatar>
                </v-row>
              </v-card-title>
              <v-card-text class="card-text-height text-left">
                <div>
                  <span class="value-style">Firewall status: {{ fireWallStatus }}</span>
                </div>
                <div>
                  <span class="value-style"
                    ><v-switch v-model="fireWallStatus" @change="enableFirewall" label="Enable Firewall" color="exportColor"></v-switch
                  ></span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col>
            <v-card elevation="2" outlined tile class="card-height">
              <v-card-title>
                <h5>Rule list</h5>
                <v-row align="center" justify="end">
                  <v-avatar size="35">
                    <img alt="logo" :src="logo" />
                  </v-avatar>
                </v-row>
              </v-card-title>
              <v-card-text class="card-text-height">
                <div class="text-left" v-for="(rule, index) of portRules" :key="index">
                  <span class="value-style">{{ rule.index }} | {{ rule.port }} | {{ rule.action }}</span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </template>
    </vertical-height-container>
    <v-dialog v-model="dialog" max-width="400px">
      <v-card>
        <v-card-title>
          <span class="headline">Update</span>
        </v-card-title>
        <v-card-text>
          <update-networking :save="onSave" :cancel="onCancelUpdate" :item="currentItem"></update-networking>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-card-text>
</template>

<script>
import axios from '../../axios';
import PageTitle from '../Common/PageTitle.vue';
import UpdateNetworking from './UpdateNetworking';
import logo from '../../assets/nube-frog-green.png';
import VerticalHeightContainer from '../Common/VerticalHeightContainer.vue';

export default {
  name: 'networking',
  components: {
    UpdateNetworking,
    PageTitle,
    VerticalHeightContainer,
  },
  data: () => ({
    logo,
    dialog: false,
    portRules: [],
    fireWallStatus: false,
    headers: [],
    currentItem: {},
    data: [],
  }),
  computed: {
    interfaceValues() {
      return this.$store.state.setting.interfaceValues;
    },
  },
  mounted() {
    this.getData();
    this.getFirewallData();
    this.getPortRules();
  },
  methods: {
    displayInterface(val) {
      let result = val;
      const interfaceValue = this.interfaceValues.find(i => i.value === val);
      if (interfaceValue) {
        result = interfaceValue.displayValue;
      }
      return result;
    },
    getPortRules() {
      axios.get(`rubix-service/networking/ufw/rules`).then(data => {
        this.portRules = data;
      });
    },
    async enableFirewall(v) {
      const enable = v ? 'enable' : 'disable';
      await axios.post('rubix-service/networking/ufw/enable', { enable }).then(result => {
        this.$toasted.show(result.toString());
        this.getFirewallData();
      });
    },
    getFirewallData() {
      axios.get(`rubix-service/networking/ufw/status`).then(data => {
        this.fireWallStatus = data;
      });
    },
    onCancelUpdate() {
      this.dialog = false;
    },
    async onSave(data) {
      let postData;
      if (data.isDhcp) {
        postData = {
          interface: data.interface,
          network_reset: !!data.network_reset,
        };
      } else {
        postData = {
          interface: data.interface,
          network_reset: !!data.network_reset,
          ip_address: data.address,
          routers: data.gateway,
          domain_name_server: data.domain_name_server,
          netmask: data.netmask,
        };
      }
      axios
        .post(`rubix-service/networking/${data.isDhcp ? 'dhcp' : 'static'}`, postData)
        .then(r => {
          this.$toasted.show(`Success!`);
          this.dialog = false;
          this.getData();
        })
        .catch(({ message }) => {
          this.$toasted.show(message, { type: 'error' });
        });
    },
    onClickEdit(item) {
      this.dialog = true;
      this.currentItem = { ...item };
      this.currentItem = {
        ...this.currentItem,
        isDhcp: true,
        domain_name_server: this.currentItem.domain_name_server || '8.8.8.8',
      };
      if (this.currentItem.interface_usage === 'static') {
        this.currentItem = { ...this.currentItem, isDhcp: false };
      }
    },
    async getData() {
      const items = await axios.get('rubix-service/networking');
      const data = [];
      const headers = [{ text: '', value: 'name' }];
      for (const key in items) {
        data.push({ ...items[key], name: key });
      }
      const fields = items[Object.keys(items)[0]];
      for (const key in fields) {
        headers.push({ text: key, value: key });
      }
      headers.push({ text: 'Tools', value: 'tools' });
      this.headers = headers;
      this.data = data;
    },
  },
};
</script>
<style scoped>
.value-style {
  font-weight: bold;
}
</style>
