<template>
  <v-card-text :loading="isLoadingData">
    <page-title title="Services"></page-title>
    <v-spacer></v-spacer>
    <v-container fluid>
      <vertical-height-container vh="82">
        <template v-slot:children>
          <v-row no-gutter>
            <v-col
              cols="12"
              lg="4"
              md="6"
              sm="12"
              xs="12"
              v-for="appDetail in systemDefaultApps"
              :key="appDetail.service"
            >
              <app-details-view
                :isSystemService="true"
                :key="`${recompute}-${appDetail.service}`"
                :app-detail="appDetail"
                :sync-app-details="fetchSystemDefaultApps"
                :change-service-status="changeServiceStatus"
              ></app-details-view>
            </v-col>
            <v-col v-if="systemDefaultApps && systemDefaultApps.length == 0" cols="12">
              <p class="pt-10">No services are installed.</p>
            </v-col>
          </v-row>
        </template>
      </vertical-height-container>
    </v-container>
  </v-card-text>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import AppDetailsView from './AppDetailsView';
import * as appsService from '../../services/appsService';
import * as actionTypes from '../../store/actionTypes';
import * as systemSettingService from '../../services/systemSettingService';
import VerticalHeightContainer from '../Common/VerticalHeightContainer.vue';

export default {
  components: {
    AppDetailsView,
    PageTitle,
    VerticalHeightContainer,
  },
  data() {
    return {
      isLoadingData: false,
      recompute: 0,
    };
  },
  computed: {
    systemDefaultApps() {
      return this.$store.state.systemDefaultApps;
    },
  },
  mounted() {
    this.fetchSystemDefaultApps();
  },
  methods: {
    fetchSystemDefaultApps() {
      return appsService
        .fetchSystemDefaultApps()
        .then(r => {
          this.$store.commit(actionTypes.UPDATE_SYSTEM_DEFAULT_APPS, r);
          this.recompute++;
        })
        .catch(err => {
          console.error(err);
        })
        .finally(() => {
          this.isLoadingData = false;
        });
    },
    changeServiceStatus: systemSettingService.changeServiceStatus,
  },
};
</script>
