<template>
  <v-card-text>
    <vertical-height-container vh="90">
      <template v-slot:children>
        <page-title title="Host Info"></page-title>
        <div style="text-align: start">
          <v-row>
            <v-col xs="12" md="6" lg="4">
              <v-card elevation="2" outlined tile class="card-height">
                <v-card-title>
                  <h5>Reboot Host</h5>
                  <v-row align="center" justify="end">
                    <v-avatar size="35">
                      <img alt="logo" :src="logo" />
                    </v-avatar>
                  </v-row>
                </v-card-title>
                <v-card-text class="card-text-height">
                  <v-switch @change="switchReboot" label="Reboot"></v-switch>
                </v-card-text>
              </v-card>
            </v-col>

            <!-- System disc usage -->
            <v-col xs="12" md="6" lg="4">
              <v-card elevation="2" outlined tile class="card-height">
                <v-card-title>
                  <h5>System Disc Usage</h5>
                  <v-row align="center" justify="end">
                    <v-avatar size="35">
                      <img alt="logo" :src="logo" />
                    </v-avatar>
                  </v-row>
                </v-card-title>
                <v-card-text class="card-text-height">
                  <div v-for="(key, idx) in objectKey(freeDisc)" :key="idx">
                    {{ toUpperTextKey(key) }}:&nbsp;<span class="value-style">{{ freeDisc[key] }}</span>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-btn @click="freeDiscFunc()" x-small color="pink darken-2" text
                    ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-col>

            <!-- Host Device Time -->
            <v-col xs="12" md="6" lg="4">
              <v-card elevation="2" outlined tile class="card-height">
                <v-card-title>
                  <v-row>
                    <v-col cols="12" class="card-title-custom">
                      <h5 class="mr-2">Host Device Time</h5> 
                      <tooltip-icon-button
                        btnColor="grey"
                        btnIcon="mdi-information-outline"
                        buttonSize="small"
                        :btnTooltip="descriptions.HOST_DEVICE_TIME"
                        :onClick="() => {}"
                      />
                      <v-spacer />
                      <v-avatar size="35">
                        <img alt="logo" :src="logo" />
                      </v-avatar>
                    </v-col>
                  </v-row>
                </v-card-title>
                <v-card-text class="card-text-height">
                  <div v-for="(key, idx) in objectKey(time)" :key="idx">
                    {{ toUpperTextKey(key) }}:&nbsp;
                    <span class="value-style" v-if="key == 'time_utc' || key == 'time_local'">{{ time[key] }}</span>
                    <span class="value-style" v-if="key != 'time_utc' && key != 'time_local'">{{ time[key] }}</span>
                  </div>
                  <v-autocomplete
                    v-model="timezone"
                    @change="onChangeTimezone"
                    :items="timezones"
                    :label="'Timezones'"
                    persistent-hint
                  ></v-autocomplete>
                </v-card-text>
                <v-card-actions>
                  <v-btn @click="getSystemTime()" x-small color="pink darken-2" text
                    ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-col>

            <!-- System memory -->
            <v-col xs="12" md="6" lg="4">
              <v-card elevation="2" outlined tile class="card-height">
                <v-card-title>
                  <h5>System Memory</h5>
                  <v-row align="center" justify="end">
                    <v-avatar size="35">
                      <img alt="logo" :src="logo" />
                    </v-avatar>
                  </v-row>
                </v-card-title>
                <v-card-text class="card-text-height">
                  <div v-for="(key, idx) in objectKey(memory)" :key="idx">
                    {{ toUpperTextKey(key) }}:&nbsp;<span class="value-style">{{ memory[key] }}</span>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-btn @click="getSystemMemory()" x-small color="pink darken-2" text
                    ><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </div>
        <v-dialog v-model="confirmDialog" persistent max-width="290">
          <v-card>
            <v-card-title class="headline"> Confirm? </v-card-title>
            <v-card-text>Are you sure?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="nubeGreen" text @click="onCancelConfirm"> No </v-btn>
              <v-btn color="red darken-1" text @click="onConfirm()"> Yes </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>
    </vertical-height-container>
  </v-card-text>
</template>

<script>
import axios from '../../axios';
import logo from '../../assets/nube-frog-green.png';
import VerticalHeightContainer from '../Common/VerticalHeightContainer.vue';
import PageTitle from '../Common/PageTitle';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import { toUpperTextKey } from '../../utils/utils';
import { DESCRIPTIONS } from '../../constants/descriptionsConstant';

export default {
  components: { PageTitle, VerticalHeightContainer, TextFieldDisplay, TooltipIconButton },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    logo,
    freeDisc: {},
    action: '',
    timezone: '',
    timezones: [],
    time: {},
    memory: {},
    confirmDialog: false,
    objectKey: Object.keys,
    descriptions: DESCRIPTIONS,
  }),
  async mounted() {
    this.freeDiscFunc();
    this.getSystemTime();
    this.getSystemMemory();
    this.getTimezones();
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  methods: {
    onChangeTimezone() {
      this.confirmDialog = true;
      this.action = 'changeTimezone';
    },
    changeTimezone() {
      axios
        .post(`timezones`, { timezone: this.timezone })
        .then(result => {
          this.$toasted.show('success');
        })
        .catch(({ message }) => {
          this.$toasted.show(message, { type: 'error' });
        });
    },
    getTimezones() {
      axios.get(`timezones`).then(result => {
        this.timezones = result;
      });
    },
    onCancelConfirm() {
      this.confirmDialog = false;
      this.timezone = '';
      this.action = '';
    },
    async restartHost() {
      const result = await axios.post('rubix-service/system/host/restart', { action: 'restart' });
      this.$toasted.show(result.toString());
    },
    onConfirm() {
      this.confirmDialog = false;
      this[this.action]();
    },
    switchReboot(v) {
      if (v) {
        this.action = 'restartHost';
        this.confirmDialog = true;
      }
    },
    freeDiscFunc() {
      axios.get(`rubix-service/disc`).then(data => {
        this.freeDisc = data;
      });
    },
    getSystemTime() {
      axios.get(`rubix-service/time`).then(data => {
        if (data.tz_local) {
          data.tz_local.forEach((element, index) => {
            const key = `tz_local_[${index}]`;
            data = { ...data, [key]: element };
          });
          delete data.tz_local;
        }
        this.time = data;
      });
    },
    getSystemMemory() {
      axios.get(`rubix-service/memory`).then(data => {
        this.memory = data;
      });
    },
  },
};
</script>
<style scoped>
.value-style {
  font-weight: bold;
}
.card-height {
  height: 250px;
}
.card-text-height {
  height: 140px;
}
</style>

